.Observacao2 .psdoprojeto table{
  position: absolute;
  top: 14%;
    color: white;
  background-color: #252525;
  border: 1px solid #fb8218;
  border-radius: 8px;
  padding: 2%;
  width: 30%; 
  float: left; 
  margin-left: 4%;
}

.psdoprojeto table tr{
    width: 30%;
    height: 100%;
    text-align: left;
}
  
.psdoprojeto table td{
    padding: 5%;
}
    
.psdoprojeto input{
    width: 70%;
    height: 100%;
    border-bottom: 1px solid white;
    background-color: #252525;
    border-right: none;
    border-top: none;
    border-left: none;
    padding: 2%;
}

.conceito table{
    width: 90%;
    margin: auto;
    border-spacing: 0;
    color: white;
}
  
.conceito table th{
    background-color: #fb8218;
    border: 1px solid #fb8218;
}
  
.conceito .primeiro-canto{
    border-radius: 15px 0 0 0;
}
  
.conceito .segundo-canto{
    border-radius: 0 15px 0 0 ;
}
  
.conceito .terceiro-canto{
    border-radius: 0 0 15px 0 ;
}
  
.conceito .quarto-canto{
    border-radius: 0 0 0 15px;
}
  
.conceito table td{
    background-color: #252525;
    border: 1px solid #fb8218;
}
  
.conceito table input{
    background-color: #252525;
    border: none;
    width: auto;
    height: auto;
}

.conceito p{
    position: relative;
    margin-bottom: 1.5%;
    font-size: 30px;
    text-align: center;
}

.close_obs {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 2%;
    top: 2.6%;
  }
  .dot2_obs {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: rgb(248, 225, 14);
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 4%;
    top: 2.6%;
  }
  .dot3_obs {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: rgb(45, 202, 25);
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 6%;
    top: 2.6%;
  }

